<template>
	<v-container>
		<v-dialog v-model="dialog" persistent max-width="700px">
			<v-card>
				<v-card-title>
					<span class="headline">{{ formTitle }} Ans</span>
				</v-card-title>
				<v-card-text>
					<v-form ref="form" v-model="valid" lazy-validation>
						<v-container>
							<v-row>
								<v-col cols="12" md="4">
									<v-select
										:items="dataPeriodos"
										item-text="periodo"
										item-value="id"
										label="Periodo"
										v-model="editItem.periodo"
										:rules="[v => !!v || 'Campo requerido']"
										required
									></v-select>
								</v-col>
								<v-col cols="12" md="4">
									<v-select
										:items="paises"
										item-text="text"
										item-value="id"
										label="Pais"
										v-model="editItem.pais"
										:rules="[v => !!v || 'Campo requerido']"
										:disabled="disabledPais"
										required
									></v-select>
								</v-col>
								<v-col cols="12" md="4">
									<v-select
										:items="dataProyectos"
										item-text="proyecto"
										item-value="id"
										label="Proyecto"
										v-model="editItem.proyecto"
										:rules="[v => !!v || 'Campo requerido']"
										required
									></v-select>
								</v-col>
							</v-row>
							<v-row>
								<v-col cols="12" md="4">
									<v-select
										:items="tipo_formato"
										label="Tipo de formato"
										v-model="editItem.tipo_formato"
										:rules="[v => !!v || 'Campo requerido']"
										required
									></v-select>
								</v-col>
                <v-col cols="12" md="8">
									<v-text-field label="Servicio" v-model="editItem.servicio" :rules="[v => !!v || 'Campo requerido']" 
										required
									></v-text-field>
								</v-col>								
							</v-row>
							<v-row>
								<v-col cols="12" md="8">
									<v-text-field label="Ind. de Servicio" v-model="editItem.indicadores_servicio" 
										:rules="[v => !!v || 'Campo requerido']" required
									></v-text-field>
								</v-col>
                <v-col cols="12" md="4">
									<v-text-field v-model="editItem.nso" prefix="%" label="NSO" type="number" min="1" max="100" 
										:rules="[v => !!v || 'Campo requerido', v => v > 0 || 'Debe ser mayor a 0', v => v <= 100 || 'Debe ser menor a 100']"
										@change="change_nso" required>
									</v-text-field>
								</v-col>
							</v-row>
              <v-row>
                <v-col cols="12" md="4">
									<v-text-field v-model="editItem.nso_dec" type="number" label="NSO DEC" required></v-text-field>
								</v-col>
								<v-col cols="12" md="4">
									<v-select :items="ind_txt_items" v-model="editItem.ind_txt" label="IND TEX" :rules="[v => !!v || 'Campo requerido']" required></v-select>
								</v-col>
								<v-col cols="12" md="4">
									<v-text-field v-model="editItem.obtenido" prefix="%" type="number" min="1" max="100" label="Obtenido" 
										:rules="[v => !!v || 'Campo requerido', v => v > 0 || 'Debe ser mayor a 0', v => v <= 100 || 'Debe ser menor a 100']"
										@change="change_obtenido" required>
									</v-text-field>
								</v-col>
							</v-row>
							<v-row>
                <v-col cols="12" md="9">
									<v-text-field v-model="editItem.observaciones" label="Observaciones"></v-text-field>
								</v-col>
								<v-col cols="12" md="3">
									<v-text-field v-model="editItem.cumplimiento_general" label="Cump. Gral" disabled type="number"
									:rules="[v => !!v || 'Campo requerido']" required></v-text-field>
								</v-col>
							</v-row>
						</v-container>
					</v-form>
				</v-card-text>
				<v-card-actions>
					<v-spacer></v-spacer>
					<v-btn color="blue darken-1" text @click="close()">Cancelar</v-btn>
					<v-btn color="blue darken-1" text @click="save()">Guardar</v-btn>
				</v-card-actions>
			</v-card>
		</v-dialog>
	</v-container>
</template>
<script>
  import http from '@/plugins/axios'
  import swal from 'sweetalert'
  import { mapState } from 'vuex'
  export default {
    name: 'EditAns',
    data: () => ({
			valid: true,
			ind_txt_items: ['<','<=','>=','>']
    }),
    methods: {  
			change_nso(value){
				if(value > this.editItem.obtenido){
					this.editItem.cumplimiento_general = -1
				}else{
					this.editItem.cumplimiento_general = 1
				}
			},
			change_obtenido(value){
				if(value >= this.editItem.nso){
					this.editItem.cumplimiento_general = 1
				}else{
					this.editItem.cumplimiento_general = -1
				}
			},
      close() {
        this.$store.commit('updateShowEdit')
				this.$store.commit('updateEditItem', {})
				this.$store.commit('setFormTitle', '')
				this.$refs.form.resetValidation()
      },
      put(url, params){
        http.put(url, params)
        .then((response) => {
          let data = response.data
          this.$store.commit("updateDataAns", data)
          swal("Actualizado Correctamente!", {
            icon: "success",
          });
        })
        .catch(() => {
					swal("Error Interno", "Favor contactar con el administrador", "error");
        });
			},
			post(url, params){
				http.post(url, params)
					.then((response) => {
						let data = response.data
						this.$store.commit("newAns", data)
						swal("Creado Correctamente!", {
							icon: "success",
						});
					})
					.catch(() => {
						swal("Error Interno", "Favor contactar con el administrador", "error");
					});
			},
			calcular_cumplimiento(){

			},
      save(){
				if (this.$refs.form.validate()){
					let proyecto = this.editItem.proyecto
					let periodo = this.editItem.periodo
					if (typeof(proyecto) !== "object"){
						this.editItem.proyecto = {"id": proyecto}
					}else{
						this.editItem.proyecto = {"id": this.editItem.proyecto.id}
					}
					if (typeof(periodo) !== "object"){
						this.editItem.periodo = {"id": periodo}
					}else{
						this.editItem.periodo = {"id": this.editItem.periodo.id}
					}
					// if (this.formTitle === 'Editar'){
					// 	this.put(`api/ans/${this.editItem.id}`, this.editItem)
					// }else{
					// 	this.post('api/ans', this.editItem)
					// }
					// this.close()
				}
      },
    },
    computed: {
      ...mapState(['dialog', 'dataProyectos', 'editItem', 'dataPeriodos', 'dataPjs', 'editItemIndex', 'paises', 'semanas', 'formTitle', 'tipo_formato']),
			disabledPais() {
				return this.$store.getters.getDisabledPais
			},
		},
  };
</script>

<style >
	.my-input input{ text-transform: uppercase}
</style>